import React, { useState } from "react";
import { Container, Form, Button, Row, Col } from "react-bootstrap";
import YAML from "yaml";

function App() {
  const [config, setConfig] = useState({
    /* initial state as above */
  });

  const handleChange = (event) => {
    const { name, value } = event.target;
    setConfig((prevConfig) => ({
      ...prevConfig,
      [name]: value,
    }));
  };

  const handleNestedChange = (event, index, field, section) => {
    const { value } = event.target;
    const updatedSection = config[section].map((item, idx) =>
      idx === index ? { ...item, [field]: value } : item
    );
    setConfig((prevConfig) => ({
      ...prevConfig,
      [section]: updatedSection,
    }));
  };

  const generateYAML = () => {
    const yamlStr = YAML.stringify(config);
    console.log(yamlStr);
  };

  return (
    <Container>
      <h1 className="my-4">YAML Generator</h1>
      <Form>
        <Form.Group controlId="version">
          <Form.Label>Version</Form.Label>
          <Form.Control
            type="text"
            name="version"
            value={config.version}
            onChange={handleChange}
          />
        </Form.Group>

        {/* Repeat similar structures for other main fields */}

        <h3>Transferts</h3>
        {config.transferts.map((transfer, index) => (
          <div key={index}>
            <Form.Group controlId={`transfertName${index}`}>
              <Form.Label>Name</Form.Label>
              <Form.Control
                type="text"
                value={transfer.name}
                onChange={(e) =>
                  handleNestedChange(e, index, "name", "transferts")
                }
              />
            </Form.Group>
            {/* Add more fields for each transfer object */}
          </div>
        ))}

        <Button variant="primary" onClick={generateYAML}>
          Generate YAML
        </Button>
      </Form>
    </Container>
  );
}

export default App;
